/* Logo */
.image-banner{
    width:20rem;
    height: fit-content;

}

 /* Course Cards  */

.card-header{
    font-weight: bold;
    font-size: 19px;
    text-align: center;
    background-color: rgb(87, 84, 84);
    color: goldenrod;
}
.amount{
    color: rgb(201, 196, 196);
}
.card-title{
    color: rgb(154, 148, 148);
    text-align: center;
    
}
.card{
    border-color: goldenrod;
    border-radius: 5px;
    color: white;
  
}
.card-body{
    background-color: rgb(4, 4, 4);
}
.card-text{
    letter-spacing: 0.8px;
    line-height: 50px;
    padding: 0;
}
.apply-button{
    
    background-color: goldenrod;
    text-decoration: none;
    
}